import {
  FileOutlined,
  FileZipOutlined,
  HomeOutlined,
  MessageOutlined,
  SettingOutlined,
  TeamOutlined,
  WalletOutlined,
} from "@ant-design/icons";
import { Badge, Layout, Menu } from "antd";
import { Link } from "react-router-dom";
import Logo from "./Logo";

const { Sider } = Layout;

function getItem(label, key, icon, children) {
  return {
    key,
    icon,
    children,
    label,
  };
}

function getLabel(label, count, path) {
  return path ? (
    <Link
      to={path}
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </Link>
  ) : (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </div>
  );
}

const SiderBar = ({ collapsed, setCollapsed }) => {
  const items = [
    getItem(<Link to="/">Bosh sahifa</Link>, "1", <HomeOutlined />),
    getItem(
      getLabel("Yangi shartnomalar", !collapsed ? 5 : 0, "/yangi_shartnomalar"),
      "2",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <FileOutlined className="white" />
        </Badge>
      ) : (
        <FileOutlined />
      )
    ),
    getItem(
      getLabel("Ishlar", !collapsed ? 5000 : 0, ""),
      "sub1",
      collapsed ? (
        <Badge count={5000} offset={[10, 15]}>
          <WalletOutlined className="white" />
        </Badge>
      ) : (
        <WalletOutlined />
      ),
      [
        getItem(<Link to="/barcha_ishlar">Barcha ishlar</Link>, "3"),
        getItem(getLabel("Ishchi dastur", 451, "/ishchi_dastur"), "4"),
        getItem(getLabel("Dala nazorati", 4, "/dala_nazorati"), "5"),
        getItem(getLabel("Komerial nazorat", 5, "/komerial_nazorat"), "6"),
        getItem(
          getLabel("Bosh geodeziya nazorati", 8, "/bosh_geodeziya_nazorati"),
          "7"
        ),
      ]
    ),
    getItem(
      getLabel("Xodimlar", !collapsed ? 5 : 0, "/xodimlar"),
      "8",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <TeamOutlined className="white" />
        </Badge>
      ) : (
        <TeamOutlined />
      )
    ),
    getItem(
      getLabel("Xabarlar", !collapsed ? 5 : 0, "/xabarlar"),
      "9",
      collapsed ? (
        <Badge count={5} offset={[10, 15]}>
          <MessageOutlined className="white" />
        </Badge>
      ) : (
        <MessageOutlined />
      )
    ),
    getItem(<Link to="/arxiv">Arxiv</Link>, "10", <FileZipOutlined />),
    getItem(
      <Link to="/sozlanmalar">Sozlanmalar</Link>,
      "11",
      <SettingOutlined />
    ),
  ];
  return (
    <Sider
      width={"290px"}
      style={{
        overflow: "auto",
        height: "100vh",
        position: "sticky",
        left: 0,
        top: 0,
        bottom: 0,
      }}
      collapsed={collapsed}
      onCollapse={(value) => setCollapsed(value)}
    >
      <div className="logo">
        <Logo collapsed={collapsed} />
      </div>
      <Menu
        defaultOpenKeys={["sub1"]}
        items={items}
        theme="dark"
        defaultSelectedKeys={["1"]}
        mode="inline"
      ></Menu>
    </Sider>
  );
};

export default SiderBar;
