import { EyeOutlined } from "@ant-design/icons";
import {
  Badge,
  Button,
  Calendar,
  Col,
  Modal,
  Radio,
  Row,
  Select,
  Table,
} from "antd";
import React, { useState } from "react";

const getListData = (value) => {
  let listData;
  switch (value.date()) {
    case 8:
      listData = [
        {
          type: "warning",
        },
      ];
      break;
    case 9:
      listData = [
        {
          type: "danger",
        },
      ];
      break;

    default:
  }

  return listData || [];
};
const CalendarComponent = () => {
  const [openModal, setOpenModal] = useState(false);
  const dateCellRender = (value) => {
    const listData = getListData(value);
    return (
      <>
        {listData.map((item, index) => (
          <Badge
            onClick={() => setOpenModal(true)}
            key={index}
            count={5}
            style={{
              backgroundColor: item.type === "warning" && "goldenrod",
            }}
          />
        ))}
      </>
    );
  };

  /* ------------------------for table--------------------------- */
  const dataSource = [
    {
      key: "name",
      name: "1",
      buyurtmachi: "dfasfsv",
      obyekt_nomi: "sdsmfvadfb sdsmfvadfb sdsmfvadfb sdsmfvadfbsdsmfvadfb",
      ish_turi: "fsdfasdfas",
      bajarish_muddati: "fasfsdfas",
    },
    {
      key: "name",
      name: "2",
      buyurtmachi: "dfasfsv",
      obyekt_nomi: "sdsmfvadfb",
      ish_turi: "fsdfasdfas",
      bajarish_muddati: "fasfsdfas",
    },
  ];

  const columns = [
    {
      title: "#",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Buyurtmachi",
      dataIndex: "buyurtmachi",
      key: "buyurtmachi",
    },
    {
      title: "Obyekt nomi",
      dataIndex: "obyekt_nomi",
      key: "obyekt_nomi",
    },
    {
      title: "Ish turi",
      dataIndex: "ish_turi",
      key: "ish_turi",
    },
    {
      title: "Bajarish muddati",
      dataIndex: "bajarish_muddati",
      key: "bajarish_muddati",
    },
    {
      title: "Tanishish",
      dataIndex: "tanishish",
      key: "tanishish",
      render: () => (
        <Button type="info" icon={<EyeOutlined />}>
          Tanishish
        </Button>
      ),
    },
  ];
  return (
    <div className="calender-container">
      <Modal
        title="Kunga tegishli ma'lumotlar"
        visible={openModal}
        footer={false}
        width={1000}
        onCancel={() => setOpenModal(false)}
      >
        <Table
          columns={columns}
          dataSource={dataSource}
          bordered
          size="small"
        />
      </Modal>
      <Calendar
        dateCellRender={dateCellRender}
        headerRender={({ value, type, onChange, onTypeChange }) => {
          const start = 0;
          const end = 12;
          const monthOptions = [];
          const current = value.clone();
          const localeData = value.localeData();
          const months = [
            "Yan",
            "Fev",
            "Mar",
            "Apr",
            "May",
            "Iyn",
            "Iyl",
            "Avg",
            "Sen",
            "Okt",
            "Noy",
            "Dek",
          ];

          for (let i = 0; i < 12; i++) {
            current.month(i);
            months.push(localeData.monthsShort(current));
          }

          for (let index = start; index < end; index++) {
            monthOptions.push(
              <Select.Option className="month-item" key={`${index}`}>
                {months[index]}
              </Select.Option>
            );
          }

          const month = value.month();
          const year = value.year();
          const currentYear = new Date().getFullYear();
          const options = [];

          for (let i = year - (year - 1960); i <= currentYear; i += 1) {
            if (i >= 1960) {
              options.push(
                <Select.Option key={i} value={i} className="year-item">
                  {i}
                </Select.Option>
              );
            }
          }

          return (
            <div
              style={{
                padding: 8,
              }}
            >
              <Row gutter={8}>
                <Col>
                  <Radio.Group
                    onChange={(e) => onTypeChange(e.target.value)}
                    value={type}
                  >
                    <Radio.Button value="month">Oy</Radio.Button>
                    <Radio.Button value="year">Yil</Radio.Button>
                  </Radio.Group>
                </Col>
                <Col>
                  <Select
                    dropdownMatchSelectWidth={false}
                    className="my-year-select"
                    onChange={(newYear) => {
                      const now = value.clone().year(Number(newYear));
                      onChange(now);
                    }}
                    value={String(year)}
                  >
                    {options}
                  </Select>
                </Col>
                <Col>
                  <Select
                    dropdownMatchSelectWidth={false}
                    value={String(month)}
                    onChange={(selectedMonth) => {
                      const newValue = value.clone();
                      newValue.month(parseInt(selectedMonth, 10));
                      onChange(newValue);
                    }}
                  >
                    {monthOptions}
                  </Select>
                </Col>
              </Row>
            </div>
          );
        }}
      />
    </div>
  );
};

export default CalendarComponent;
